import * as React from 'react';
import './userContent.less';

interface IUserContentProps {
    id:string
    username:string
    type:number
    nickname:string
    icon:string
    phone:string
    regtime:string
    lastlogintime:string
    isauthent:number
    ct:string
    mt:string
}

export class UserContent extends React.Component<IUserContentProps> {
  public render() {
    return (
      <div className="userContent">
        <p>
            用户名：{this.props.username}
        </p>
        <p>
            类型：{this.props.type === 1?"个体用户":"普通用户"}
        </p>
        <p>
            昵称：{this.props.nickname}
        </p>
        <p>
            头像：<img src={this.props.icon}/>
        </p>
        <p>
            手机：{this.props.phone}
        </p>
        <p>
            注册时间：{this.props.regtime}
        </p>
        <p>
            最后登录时间：{this.props.lastlogintime}
        </p>
        <p>
            是否实名认证：{this.props.isauthent === 1?"已认证":"未认证"}
        </p>
        <p>
            创建时间：{this.props.ct}
        </p>
        <p>
            修改时间：{this.props.mt}
        </p>
      </div>
    );
  }
}
